<template>
    <yd-layout>
        <yd-navbar title="会议室预定记录" bgcolor="#70b6ef" color="#fff" fontsize="0.36rem">
            <router-link to="#" slot="left">
                <yd-navbar-back-icon color="#fff">
                    <slot color="#fff">返回</slot>
                </yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="schedules resHistory">
            <yd-tab>
                <yd-tab-panel label="未开始" :active="labeling">
                    <div class="mainContent" v-for="(item, index) in listGroup" :key="index">
                        <div class="contentSradius">
                            <div class="fluidWidth" @click="viewDetail(item.meetId, true)">
                                <yd-flexbox>
                                    <div class="historyTime" v-text="item.time.split(' ')[0]"></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div class="title" v-text="item.meettitle"></div>
                                    <div class="blueWord" v-text="'('+item.meetType+')'"></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div>会议时间：</div>
                                    <div v-text="item.time.substr(10)"></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div>会议地点：</div>
                                    <div v-text="item.meetroom"></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div>会议提醒：</div>
                                    <div v-text="item.remind == null? '': item.remind"></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div class="title detail">查看详情 >></div>
                                </yd-flexbox>
                            </div>
                        </div>
                    </div>
                </yd-tab-panel>
                <yd-tab-panel label="历史记录" :active="hislabel">
                    <div class="mainContent" v-for="(value, index) in historyGroup" :key="index">
                        <div class="contentSradius">
                            <div class="fluidWidth" @click="viewDetail(value.meetId, true)">
                                <yd-flexbox>
                                    <div class="historyTime2" v-text="value.time.split(' ')[0]"></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div class="title" v-text="value.meettitle"></div>
                                    <div class="blueWord" v-text="'('+value.meetType+')'"></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div>会议时间：</div>
                                    <div v-text="value.time.substr(10)">下午 14:00-15:00</div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div>会议地点：</div>
                                    <div v-text="value.meetroom"></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div>会议提醒：</div>
                                    <div v-text="value.remind == null? '': value.remind"></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div class="title detail">查看详情 >></div>
                                </yd-flexbox>
                            </div>
                            <img class="endLogo" src="../../../static/images/conference/6.png">
                        </div>
                    </div>
                </yd-tab-panel>
            </yd-tab>
        </div>
        <yd-tabbar class="schedules" slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999">
            <yd-tabbar-item link="/appointment" title="预约会议室">
                <yd-icon name="unStart" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item title="会议室预定记录" link="" active>
                <yd-icon name="unAttend" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    data() {
        return {
            listGroup: [],
            historyGroup: [],
            labeling: false,
            hislabel: false
        }
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    mounted: function () {
        let vm = this;
        document.setTitle('会议室预定记录');
        vm.initInfo();
        vm.initHistory();
        vm.$route.params.label == 2 ? vm.hislabel = true : vm.labeling = true;
    },
    methods: {
        // 已预订会议信息列表
        initInfo() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/list', {
                'name': vm.getUserInfo.ssotoken
            }, function (res) {
                vm.listGroup = res.myRbLst;
            })
        },
        // 历史会议信息
        initHistory() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/historyMeet', {
                'name': vm.getUserInfo.ssotoken
            }, function (res) {
                vm.historyGroup = res.myRbLst;
            })
        },
        // 查看详情
        viewDetail(meetid, flag) {
            let vm = this;
            vm.$router.push({
                name: 'meetDetails',
                params: {
                    meetId: meetid,
                    detype: flag
                }
            })
        }
    }
}
</script>
<style scoped lang="less">
.historyTime2 {
    background-color: #dedede !important;
}

.endLogo {
    width: 1rem;
    position: absolute;
    top: 0;
    right: 0;
}

.detail {
    width: 100%;
}

.yd-icon-unStart:before,
.yd-icon-unAttend:before,
.yd-icon-start:before,
.yd-icon-attend:before {
    display: block;
    content: '';
}

.schedules {
    .yd-icon-unStart:before {
        width: .41rem;
        height: .41rem;
        background: url(../../../static/images/booking3@2x.png) no-repeat 0 0;
        background-size: .4rem;
    }
    .yd-icon-unAttend:before {
        width: .41rem;
        height: .41rem;
        background: url(../../../static/images/booking1@2x.png) no-repeat 0 0;
        background-size: .4rem;
    }
    .yd-tabbar-txt {
        color: #59a5fd;
    }
}
</style>


